.logo {
    margin-left: auto;
    margin-right: auto;
    height: 60px;
    margin-top: 10px;
    margin-bottom: 10px;
    display: flex;
    cursor: pointer;
}

.main {
    width: 100%;
    height: 100vh;
    object-fit: cover;
    background-color: #eeeeee;
    padding: 0px;
    margin: 0px;
    display: block;
}

.button {
    position: absolute;
    display: flex;
    justify-content: center;
    flex-direction: column;
    color: white;
    font-size: 20pt;
    white-space: nowrap;
    text-align: center;
    font-family: "Lato", sans-serif;
    top: 85%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 320px;
    height: 70px;
    background: #61466F;
    color: white;
    border-radius: 25px;
    cursor: pointer;
}

.button:hover { 
    background: #846891; 
    color: white; 
}

.title {
    position: absolute;
    top: 65%;
    left: 50%;
    transform: translate(-50%, -50%);
    color: white;
    font-family: 'LEMON MILK', sans-serif;
    font-weight: bold;
    font-size: 35pt;
    white-space: nowrap;
}

.signin {
    word-spacing: 3px;
}

ul { 
    background-color: #735880; 
    list-style-type: none; 
    margin: 0; 
    padding: 0; 
    overflow: hidden;
}

li { 
    float: left; 
    cursor: pointer;
}

li a, .dropbtn { 
    display: block; 
    background-color: #735880; 
    color: #fff; 
    padding: 10px;
    padding-right: 25px; 
    padding-left: 25px;
    text-decoration: none;
    text-align: center; 
    font-family: "Lato", sans-serif;
    font-size: 100%;
}


li a:hover  { 
    background-color: #a188ac; 
    color: white; 
}

body{
    margin:0
}

li.dropdown {
    display: inline-block;
}

.dropdown-content {
    display: none;
    position: absolute;
    min-width: 180px;
    box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
}

.dropdown-content a {
    color:black;
    background-color:rgb(216, 216, 216);
    padding: 15px 16px;
    text-decoration: none;
    display: block;
    text-align: left;
}

.dropdown-content a:hover {
    background-color: #f1f1f1;
    color: black;
    z-index: 10;
}

.dropdown:hover .dropdown-content {
    display: block;
    z-index: 10;
}

